.order_card {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 16px 24px 16px 16px;
    background-color: var(--white);
}

.order_card__main_container {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
}

.order_card__date_container {
    display: flex;
    flex-direction: column;
    margin: 0 0 8px 12px;
    padding: 0;
}

.order_card__date {
    font-family: 'Montserrat', 'Arial', sans-serif;
    font-size: var(--font-size-16);
    font-style: var(--font-style-normal);
    font-weight: var(--font-weight-regular);
    color: var(--black);

    margin: 0;
    padding: 0;
}

.order_card__time {
    font-family: 'Montserrat', 'Arial', sans-serif;
    font-size: var(--font-size-16);
    font-style: var(--font-style-normal);
    font-weight: var(--font-weight-regular);
    color: var(--dark-grey);

    margin: 0;
    padding: 0;
}

.order_card__icons_container {
    display: flex;
}

.order_card__icon {
    height: 24px;
    height: 24px;
}

.order_card__description {
    font-family: 'Montserrat', 'Arial', sans-serif;
    font-size: var(--font-size-16);
    font-style: var(--font-style-normal);
    font-weight: var(--font-weight-regular);
    color: var(--dark-grey);

    margin: 0;
    padding: 0;
}

.order_card__routh_container {
    display: flex;
}

.order_card__routh_icons_container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 12px;
}

.order_card__routh_point_icon {
    display: flex;
    height: 8px;
}

.order_card__routh_point_icon:first-child {
    margin: 6px 0 0 0;
}

.order_card__routh_point_icon:last-child {
    margin: 0 0 6px 0;
}

.order_card__routh_arrow_icon {
    display: flex;
}

.order_card__towns_container {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin: 0;
    padding: 0;
}

.order_card__town {
    font-family: 'Montserrat', 'Arial', sans-serif;
    font-size: var(--font-size-16);
    font-style: var(--font-style-normal);
    font-weight: var(--font-weight-semibold);
    color: var(--black);

    margin: 0;
    padding: 0;
}

.order_card__secondary_container {
    display: flex;
    justify-content: space-between;
    margin: 8px 0 0 12px;
    padding: 0;
}

.order_card__price {
    font-family: 'Montserrat', 'Arial', sans-serif;
    font-size: var(--font-size-22);
    font-style: var(--font-style-normal);
    font-weight: var(--font-weight-bold);
    color: var(--green);

    margin: 0;
    padding: 0;
}

.order_card__price {
    margin: 0;
    padding: 0;
}

.order_card__like_container {
    display: flex;
}

.order_card__like_title {
    font-family: 'Montserrat', 'Arial', sans-serif;
    font-size: var(--font-size-16);
    font-style: var(--font-style-normal);
    font-weight: var(--font-weight-regular);
    color: var(--light-grey);

    margin: 0;
    padding: 0;
}

.order_card__icon {
    height: 24px;
    height: 24px;
}